import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import isEmpty from "lodash/isEmpty";
import {
  useTable,
  useResizeColumns,
  useFlexLayout,
  usePagination,
  useSortBy,
  useRowSelect
} from "react-table";
import Collapse from "@mui/material/Collapse";
import IconButton from "@mui/material/IconButton";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import KeyboardArrowUpIcon from "@mui/icons-material/KeyboardArrowUp";
import MaUTable from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import TableSortLabel from "@mui/material/TableSortLabel";
import Paper from "@mui/material/Paper";
import { injectIntl } from "react-intl";
import GridFooter from "./GridFooter";
import { GridLoader, GridNoDataText } from "../datagrid/utils";
import { ID_PREFIX } from "../../../configs/IdConfig";

/**
 * Re-usable data grid with customizable columns, records view
 * and a pagination footer.
 */

function DataGrid(props) {
  // const [resetSelectedRows, setResetSelectedRows] = useState(false);
  const resetSelectedRows = false;
  const [prevSortedColumn, setPrevSortedColumn] = useState(null);
  const gridcolumn = React.useMemo(() => [...props.columns], [props.columns]);
  const gridData = React.useMemo(() => [...props.data], [props.data]);
  let testId = props && props.testId ? `${ID_PREFIX}${props.testId}` : "";
  let SubComponent = props && props.SubComponent;

  // Console added for checking refrence equality of props in local
  // console.log("pross", props);
  // const [data0, setData0] = useState([]);
  // const gridData = React.useMemo(() => {

  //   setData0(props);
  //   return [...props.data];
  // }, [props.data]);

  // useEffect(() => {
  //   console.log("availableBulkActions->>data changed");
  // }, [props.availableBulkActions]);

  // useEffect(() => {
  //   console.log("SubComponent->>data changed");
  // }, [props.SubComponent]);

  // useEffect(() => {
  //   console.log("allSelected->>data changed");
  // }, [props.allSelected]);

  // useEffect(() => {
  //   console.log("canNext->>data changed");
  // }, [props.canNext]);
  // useEffect(() => {
  //   console.log("canPrevious->>data changed");
  // }, [props.canPrevious]);

  // useEffect(() => {
  //   console.log("checkedEntities->>data changed");
  // }, [props.checkedEntities]);
  // useEffect(() => {
  //   console.log("checkedItems->>data changed");
  // }, [props.checkedItems]);

  // useEffect(() => {
  //   console.log("columns->>data changed");
  // }, [props.columns]);
  // useEffect(() => {
  //   console.log("data->>data changed");
  // }, [props.data]);

  // useEffect(() => {
  //   console.log("defaultPageSize->>data changed");
  // }, [props.defaultPageSize]);
  // useEffect(() => {
  //   console.log("dispatch->>data changed");
  // }, [props.dispatch]);

  // useEffect(() => {
  //   console.log("freezeWhenExpanded->>data changed");
  // }, [props.freezeWhenExpanded]);
  // useEffect(() => {
  //   console.log("getData->>data changed");
  // }, [props.getData]);

  // useEffect(() => {
  //   console.log("hideCheckbox  >>data changed");
  // }, [props.hideCheckbox]);
  // useEffect(() => {
  //   console.log("isSingleSelect  ->>data changed");
  // }, [props.isSingleSelect]);

  // useEffect(() => {
  //   console.log("keyAsTestId  >>data changed");
  // }, [props.keyAsTestId]);
  // useEffect(() => {
  //   console.log("manual  ->>data changed");
  // }, [props.manual]);

  // useEffect(() => {
  //   console.log("noDataText  >>data changed");
  // }, [props.noDataText]);
  // useEffect(() => {
  //   console.log("noRowsFound  ->>data changed");
  // }, [props.noRowsFoundl]);

  // useEffect(() => {
  //   console.log("onPageChange  >>data changed");
  // }, [props.onPageChange]);
  // useEffect(() => {
  //   console.log("onPageSizeChange  ->>data changed");
  // }, [props.onPageSizeChange]);

  // useEffect(() => {
  //   console.log("onResizedChange  >>data changed");
  // }, [props.onResizedChange]);
  // useEffect(() => {
  //   console.log("onRowCellClick  ->>data changed");
  // }, [props.onRowCellClick]);

  // useEffect(() => {
  //   console.log("onSortedChange  >>data changed");
  // }, [props.onSortedChange]);
  // useEffect(() => {
  //   console.log("page  ->>data changed");
  // }, [props.page]);

  // useEffect(() => {
  //   console.log("pageSize  >>data changed");
  // }, [props.pageSize]);
  // useEffect(() => {
  //   console.log("pagse  ->>data changed");
  // }, [props.pages]);

  // useEffect(() => {
  //   console.log("showPagination  >>data changed");
  // }, [props.showPagination]);
  // useEffect(() => {
  //   console.log("sorted  ->>data changed");
  // }, [props.sorted]);

  // useEffect(() => {
  //   console.log("subComponentDetails  >>data changed");
  // }, [props.subComponentDetails]);
  // useEffect(() => {
  //   console.log("testId  ->>data changed");
  // }, [props.testId]);
  // useEffect(() => {
  //   console.log("totalSize  >>data changed");
  // }, [props.totalSize]);
  // useEffect(() => {
  //   console.log("usePaginationWithApi  ->>data changed");
  // }, [props.usePaginationWithApi]);

  const {
    getTableProps,
    headerGroups,
    rows,
    prepareRow,
    page,
    gotoPage,
    nextPage,
    previousPage,
    setPageSize,
    state: { pageIndex, pageSize, sortBy, columnResizing }
  } = useTable(
    {
      columns: gridcolumn,
      data: gridData,
      id: testId,
      keyAsTestId: props.keyAsTestId,
      autoResetResize: false,
      autoResetSortBy: false,
      autoResetSelectedRows: resetSelectedRows,
      manualSortBy: props && props.usePaginationWithApi,
      initialState: { pageIndex: 0, pageSize: 25 }
    },
    useSortBy,
    useFlexLayout,
    useResizeColumns,
    usePagination,
    useRowSelect,
    hooks => {
      hooks.visibleColumns.push(columns => [
        // Let's make a column for selection
        props.SubComponent
          ? {
              id: "expand",
              width: "35px",
              maxWidth: "35px",
              minWidth: "35px",
              Header: () =>
                props.SubComponent && (
                  <div
                    style={{
                      width: 35
                    }}
                  />
                )
            }
          : {},
        ...columns
      ]);
    }
  );

  useEffect(() => {
    if (prevSortedColumn && !sortBy.length) {
      props.onSortedChange && props.onSortedChange("");
    } else {
      props.onSortedChange && sortBy.length && props.onSortedChange(sortBy);
    }
    sortBy.length && setPrevSortedColumn({ ...sortBy[0] });
  }, [sortBy]); // eslint-disable-line react-hooks/exhaustive-deps

  useEffect(() => {
    if (
      columnResizing &&
      columnResizing.columnWidths &&
      !isEmpty(columnResizing.columnWidths)
    ) {
      let newResized = [];
      let colWidths = columnResizing.columnWidths;
      let keys = Object.keys(columnResizing.columnWidths);
      for (let i = 0; i < keys.length; i++) {
        newResized.push({ id: keys[i], value: colWidths[keys[i]] });
      }
      if (!columnResizing.isResizingColumn) {
        props.onResizedChange({ newResized });
      }
    }
  }, [columnResizing]); // eslint-disable-line react-hooks/exhaustive-deps

  let gridRows = props.usePaginationWithApi ? [...rows] : [...page];
  const [open, setOpen] = useState({});
  // Render the UI for your table
  return (
    <Paper
      elevation={0}
      className={`spog-datagrid-paper ${
        !props.showPagination ? "no-pagination" : ""
      }`}
    >
      <TableContainer className="spog-datagrid-container">
        <MaUTable stickyHeader {...getTableProps()} size="small" id={testId}>
          <TableHead id={`${testId}_th`} style={{ zIndex: 2 }}>
            {headerGroups.map(headerGroup => (
              <TableRow {...headerGroup.getHeaderGroupProps()}>
                {headerGroup.headers.map(column => {
                  //eslint-disable-next-line eqeqeq
                  if (column.id == undefined) {
                    //eslint-disable-next-line array-callback-return
                    return;
                  }
                  if (props.hideCheckbox && column.id === "selection") {
                    //eslint-disable-next-line array-callback-return
                    return;
                  }
                  column.canSort = column.sortable;
                  let sortArrow =
                    column.disableSortBy ||
                    column.id === "selection" ||
                    column.id === "icon" ||
                    column.id === "available_actions" ||
                    column.id === "_rowId" ||
                    column.id === "expand" ||
                    column.id === "showErrorIcon" ? null : (
                      <>
                        {column.sortable && (
                          <TableSortLabel
                            active={true}
                            direction={"desc"}
                            className="mui-table-sort-default"
                          ></TableSortLabel>
                        )}
                      </>
                    );

                  return (
                    <TableCell
                      {...column.getHeaderProps()}
                      id={`${testId}_th_${column.id}`}
                    >
                      <div
                        {...column.getSortByToggleProps()}
                        style={{ display: "flex" }}
                      >
                        {column.render("Header")}
                        <span>
                          {column.sortable && column.isSorted ? (
                            column.isSortedDesc ? (
                              <TableSortLabel
                                active={true}
                                direction={"desc"}
                              ></TableSortLabel>
                            ) : (
                              <TableSortLabel
                                active={true}
                                direction={"asc"}
                              ></TableSortLabel>
                            )
                          ) : (
                            sortArrow
                          )}
                        </span>
                      </div>
                      <div
                        {...column.getResizerProps()}
                        className={`resizer ${
                          column.isResizing ? "isResizing" : ""
                        }`}
                      />
                    </TableCell>
                  );
                })}
              </TableRow>
            ))}
          </TableHead>
          <TableBody id={`${testId}_tbody`}>
            {gridRows.length > 0 ? (
              gridRows.map((row, i) => {
                prepareRow(row);
                return (
                  <React.Fragment>
                    <TableRow
                      {...row.getRowProps()}
                      className={`${row.original.is_deleted ? "ghost" : ""} ${
                        row && row.isSelected ? "dg_row_selected" : ""
                      } dg_row`}
                    >
                      {props.SubComponent != null && (
                        <TableCell>
                          <IconButton
                            aria-label="expand row"
                            size="small"
                            onClick={() => setOpen({ [i]: !open[i] })}
                          >
                            {open[i] ? (
                              <KeyboardArrowUpIcon />
                            ) : (
                              <KeyboardArrowDownIcon />
                            )}
                          </IconButton>
                        </TableCell>
                      )}

                      {//eslint-disable-next-line array-callback-return
                      row.cells.map(cell => {
                        if (
                          cell.column.id === "expand" ||
                          //eslint-disable-next-line eqeqeq
                          cell.column.id == undefined
                        ) {
                          //eslint-disable-next-line array-callback-return
                          return;
                        }
                        if (
                          props.hideCheckbox &&
                          cell.column.id === "selection"
                        ) {
                          //eslint-disable-next-line array-callback-return
                          return;
                        }
                        return (
                          <TableCell
                            {...cell.getCellProps({
                              onClick: () => {
                                props.onRowCellClick &&
                                  props.onRowCellClick(
                                    { id: cell.column.id },
                                    cell.row.original
                                  );
                              }
                            })}
                          >
                            {cell.render("Cell")}
                          </TableCell>
                        );
                      })}
                    </TableRow>
                    {props.SubComponent != null && (
                      <Collapse in={open[i]} timeout="auto" unmountOnExit>
                        <TableRow>
                          <TableCell style={{ padding: "0" }}>
                            <div style={{ width: "100%" }}>
                              <SubComponent
                                data={row.original}
                                getData={props.getData}
                                subComponentDetails={props.subComponentDetails}
                              />
                            </div>
                          </TableCell>
                        </TableRow>
                      </Collapse>
                    )}
                  </React.Fragment>
                );
              })
            ) : (
              <div id={`${testId}_tLoader`}>
                {props.noDataText ? (
                  <GridLoader />
                ) : (
                  <GridNoDataText noDataText={"No rows found"} />
                )}
              </div>
            )}
          </TableBody>
        </MaUTable>
      </TableContainer>
      {props.showPagination ? (
        <GridFooter
          {...props}
          pageIndex={pageIndex}
          gridPageSize={pageSize}
          setPageSize={setPageSize}
          gotoPage={gotoPage}
          nextPage={nextPage}
          previousPage={previousPage}
        />
      ) : null}
    </Paper>
  );
}

DataGrid.defaultProps = {
  keyAsTestId: true,
  noDataText: false,
  noRowsFound: false,
  defaultPageSize: 25,
  showPagination: true,
  usePaginationWithApi: true,
  hideCheckbox: false,
  allSelected: false,
  SubComponent: () => {},
  subComponentDetails: [],
  getData: () => {},
  onRowCellClick: () => {},
  onResizedChange: () => {},
  totalSize: 0,
  page: 1,
  pages: 0,
  pageSize: 25,
  canNext: false,
  canPrevious: false,
  isSingleSelect: false
};

DataGrid.propTypes = {
  keyAsTestId: PropTypes.bool,
  noDataText: PropTypes.bool,
  columns: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  data: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  defaultPageSize: PropTypes.number,
  showPagination: PropTypes.bool,
  onRowCellClick: PropTypes.func,
  onSortedChange: PropTypes.func,
  onResizedChange: PropTypes.func,
  page: PropTypes.number.isRequired,
  pages: PropTypes.number.isRequired,
  pageSize: PropTypes.number.isRequired,
  canPrevious: PropTypes.bool,
  canNext: PropTypes.bool,
  totalSize: PropTypes.number.isRequired,
  usePaginationWithApi: PropTypes.bool,
  hideCheckbox: PropTypes.bool,
  SubComponent: PropTypes.func,
  subComponentDetails: PropTypes.arrayOf(PropTypes.shape({})),
  getData: PropTypes.func,
  isSingleSelect: PropTypes.bool
};
export default React.memo(DataGrid);
