import React from "react";
import PropTypes from "prop-types";

import THeader from "./THeader";
//import TBody from "./TBody";
import TBody from "./TBody/TBody.js";
import GridSettings from "./GridSettings/index";
import CCTable from "./CCTable";

const { Provider, Consumer } = React.createContext("CCDataGrid");

export const CCDataGridProvider = Provider;
export const CCDataGridConsumer = Consumer;

const createCCDataGrid = config => {
  const Ret = ({ children = [], ...rest }) => (
    <CCTable config={config} {...rest}>
      {children}
    </CCTable>
  );

  Ret.propTypes = {
    children: PropTypes.oneOfType([
      PropTypes.node,
      PropTypes.arrayOf(PropTypes.node)
    ])
  };

  Ret.defaultProps = {
    children: []
  };

  return Ret;
};

export { 
  THeader, 
  TBody, 
  CCTable, 
  GridSettings 
};

export default createCCDataGrid;
