const styles = {
  color: {
    default: "#8f8f8f"
  },
  svg: {
    display: "inline-block",
    verticalAlign: "middle"
  }
};

const Icon = ({ icon, color, style, width, height, viewBox }) => {
  const renderPath = () => {
    if (Array.isArray(icons[icon])) {
      return icons[icon].map((item, i) => {
        let keyIndex = i;
        return (
          <path
            d={item.path}
            key={keyIndex}
            fill={item.color || color || styles.color.default}
            fillRule={item.fillRule ? item.fillRule : ""}
            clipRule={item.clipRule ? item.clipRule : ""}
          />
        );
      });
    }
    return <path d={icons[icon]} fill={color} />;
  };

  return (
    <svg
      style={style ? style : styles.svg}
      width={width}
      height={height}
      viewBox={viewBox}
    >
      {renderPath()}
    </svg>
  );
};

Icon.defaultProps = {
  color: "#8f8f8f",
  viewBox: "0 0 1024 1024"
};

Icon.propTypes = {
  icon: PropTypes.string.isRequired,
  width: PropTypes.string,
  height: PropTypes.string,
  viewBox: PropTypes.string.isRequired,
  color: PropTypes.string
};

export default React.memo(Icon);
