import React from "react";
import { connect } from "react-redux";

import * as ccDataGridSelectors from "../../../../state/selectors/ccDataGrid";
import * as ccDataGridActions from "../../../../state/actions/ccDataGrid";
import { setColumnWidth } from "../../../../state/actions/actions";
import { CCDataGridConsumer } from "../../CCDataGrid-MUI";

export const withCCDataGrid = WrappedComponent => props => (
  <CCDataGridConsumer>
    {context => <WrappedComponent ccDataGrid={context} {...props} />}
  </CCDataGridConsumer>
);

// Fix for pagination false
export const withPagination = WrappedComponent =>
  connect(
    (state, ownProps) => {
      const { ccDataGrid } = ownProps;
      let { ccDataGridId, pagination } = ccDataGrid.config;
      const { ccDataGridUniqueId } = ccDataGrid;
      if (ccDataGridUniqueId && ccDataGridUniqueId.length > 0)
        ccDataGridId = ccDataGridUniqueId;
      if (pagination === false) {
        const totalEntities = ccDataGridSelectors.getEntities(ccDataGridId)(
          state
        ).length;
        const pageSize = ccDataGridSelectors.getPageSize(ccDataGridId)(state);

        return {
          page: ccDataGridSelectors.getPage(ccDataGridId)(state),
          pages:
            Math.floor(totalEntities / pageSize) +
            Math.ceil((totalEntities % pageSize) / pageSize),
          pageSize,
          total: ccDataGridSelectors.getEntities(ccDataGridId)(state).length
        };
      }
      return {
        page: ccDataGridSelectors.getPage(ccDataGridId)(state),
        pages: ccDataGridSelectors.getPages(ccDataGridId)(state),
        pageSize: ccDataGridSelectors.getPageSize(ccDataGridId)(state),
        total: ccDataGridSelectors.getTotalSize(ccDataGridId)(state)
      };
    },
    (dispatch, ownProps) => ({
      updatePage(page, refreshGrid) {
        const { ccDataGrid } = ownProps;
        let { ccDataGridId } = ccDataGrid.config;
        const { ccDataGridUniqueId } = ccDataGrid;
        if (ccDataGridUniqueId && ccDataGridUniqueId.length > 0)
          ccDataGridId = ccDataGridUniqueId;

        dispatch(ccDataGridActions.updatePage(ccDataGridId, page, refreshGrid));
      },
      updatePageSize(pageSize, refreshGrid) {
        const { ccDataGrid } = ownProps;
        let { ccDataGridId } = ccDataGrid.config;
        const { ccDataGridUniqueId } = ccDataGrid;
        if (ccDataGridUniqueId && ccDataGridUniqueId.length > 0)
          ccDataGridId = ccDataGridUniqueId;

        dispatch(
          ccDataGridActions.updatePageSize(ccDataGridId, pageSize, refreshGrid)
        );
      },
      setColumnWidth(newResized) {
        dispatch(setColumnWidth({ newResized }));
      }
    })
  )(WrappedComponent);

export const withSorted = WrappedComponent =>
  connect(
    (state, ownProps) => {
      const { ccDataGrid } = ownProps;
      let { ccDataGridId } = ccDataGrid.config;
      const { ccDataGridUniqueId } = ccDataGrid;
      if (ccDataGridUniqueId && ccDataGridUniqueId.length > 0)
        ccDataGridId = ccDataGridUniqueId;

      return {
        sorted: ccDataGridSelectors.getSorted(ccDataGridId)(state)
      };
    },
    (dispatch, ownProps) => ({
      updateSorted(sorted, refreshGrid) {
        const { ccDataGridId } = ownProps.ccDataGrid.config;
        dispatch(
          ccDataGridActions.updateSorted(ccDataGridId, sorted, refreshGrid)
        );
      }
    })
  )(WrappedComponent);

