export const withChecked = WrappedComponent =>
  connect(
    (state, ownProps) => {
      const { ccDataGrid } = ownProps;
      let { ccDataGridId } = ccDataGrid.config;
      const { ccDataGridUniqueId } = ccDataGrid;
      if (ccDataGridUniqueId && ccDataGridUniqueId.length > 0)
        ccDataGridId = ccDataGridUniqueId;

      return {
        checked: ccDataGridSelectors.getChecked(ccDataGridId)(state),
        checkedItems: ccDataGridSelectors.getCheckedItems(ccDataGridId)(state),
        checkedEntities: ccDataGridSelectors.getCheckedEntities(ccDataGridId)(
          state
        ),
        availableBulkActions: ccDataGridSelectors.getAvailableBulkActions(
          ccDataGridId
        )(state)
      };
    },
    (dispatch, ownProps) => ({
      uncheckAll() {
        const { ccDataGrid } = ownProps;
        let { ccDataGridId } = ccDataGrid.config;
        const { ccDataGridUniqueId } = ccDataGrid;
        if (ccDataGridUniqueId && ccDataGridUniqueId.length > 0)
          ccDataGridId = ccDataGridUniqueId;
        dispatch(ccDataGridActions.uncheckAll(ccDataGridId));
      },
      checkRows(...rowIds) {
        const { ccDataGrid } = ownProps;
        let { ccDataGridId } = ccDataGrid.config;
        const { ccDataGridUniqueId } = ccDataGrid;
        if (ccDataGridUniqueId && ccDataGridUniqueId.length > 0)
          ccDataGridId = ccDataGridUniqueId;
        dispatch(ccDataGridActions.checkRows(ccDataGridId, ...rowIds));
      },
      uncheckRow(rowId) {
        const { ccDataGrid } = ownProps;
        let { ccDataGridId } = ccDataGrid.config;
        const { ccDataGridUniqueId } = ccDataGrid;
        if (ccDataGridUniqueId && ccDataGridUniqueId.length > 0)
          ccDataGridId = ccDataGridUniqueId;
        dispatch(ccDataGridActions.uncheckRow(ccDataGridId, rowId));
      }
    })
  )(WrappedComponent);
