THeader.propTypes = {
  ccDataGrid: PropTypes.shape({
    config: PropTypes.shape({
      ccDataGridId: PropTypes.oneOfType([PropTypes.number, PropTypes.string])
        .isRequired,
      disableBulkActions: PropTypes.arrayOf(PropTypes.string),
      hideGridSettings: PropTypes.bool,
      hideSaveSearchLink: PropTypes.bool,
      hideCheckbox: PropTypes.bool
    }).isRequired,
    AdvancedSearchForm: PropTypes.func,
    onActions: PropTypes.func.isRequired,
    gridHeaderButtons: PropTypes.element,
    columnsModel: PropTypes.shape([]),
    groupActionHandler: PropTypes.func,
    filterType: PropTypes.string,
    showAddSourceComponent: PropTypes.bool,
    addSourceOptionClick: PropTypes.func
  }).isRequired,
  // entities: PropTypes.arrayOf(PropTypes.shape()).isRequired,
  checkedItems: PropTypes.arrayOf(PropTypes.string).isRequired,
  checkedEntities: PropTypes.arrayOf(PropTypes.shape()).isRequired,
  availableBulkActions: PropTypes.arrayOf(PropTypes.string).isRequired,
  children: PropTypes.oneOfType(
    PropTypes.node,
    PropTypes.arrayOf(PropTypes.node)
  ).isRequired,
  options: PropTypes.shape([]),
  twoFactorRequired: PropTypes.bool,
  ccDataGridUniqueId: PropTypes.string.isRequired,
  currentOptionToTags: PropTypes.shape({}).isRequired,
  totalSize: PropTypes.number.isRequired,
  ccValue: PropTypes.string.isRequired,
  callApiForAdvancedSearch: PropTypes.func.isRequired,
  originalAdvancedSearchOptions: PropTypes.shape([]).isRequired,
};

THeader.defaultProps = {
  options: [],
  twoFactorRequired: false,
};

const mapStateToProps = (state, props) => {
  const { ccDataGrid } = props;
  const ccValue = ccDataGrid.ccDataGridUniqueId || ccDataGrid?.config?.ccDataGridId;
  return {
    twoFactorRequired:
      state.login.organizationInfo &&
      state.login.organizationInfo.two_factor_required,
    currentOptionToTags: ccDataGridSelectors.getCCDataGridOptionToTags(ccValue)(
      state
    ),
    options: getAdvancedSearchOptions(ccValue)(state),
    originalAdvancedSearchOptions: ccDataGridSelectors.getAdvancedSearchOptionsUnChanged(
      ccValue
    )(state),
    ccValue,
    searchOption: getAdvancedSearchCurrentSearchCriteria(ccValue)(state)
  };
};

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      callApiForAdvancedSearch: ccDataGridId =>
        dispatch(ccDataGridActions.fetchData(ccDataGridId)),
      ccDataGridShowCurrentSearch: (ccDataGridId, value) =>
        dispatch(
          ccDataGridActions.isAdvancedSearchOptionsSelected(ccDataGridId, value)
        )
    },
    dispatch
  );

export default compose(
  withCCDataGrid,
  withLoading,
  withEntities,
  withChecked,
  connect(mapStateToProps, mapDispatchToProps)
)(THeader);
