/**
 * Returns job status color based on status of job
 */
const handleJobStatusColor = status => {
  switch (status) {
    case JOB_STATUS.LICENSE_FAILED:
    case JOB_STATUS.CANCELED:
    case JOB_STATUS.CRASH:
    case JOB_STATUS.FAILED:
    case JOB_STATUS.MISSED:
    case JOB_STATUS.STOP:
      return {
        color: "red"
      };
    case JOB_STATUS.SKIPPED:
    case JOB_STATUS.INCOMPLETE:
    case JOB_STATUS.WARNING:
    case JOB_STATUS.WAITING:
      return {
        color: "#eaa433"
      };
    default:
      return {
        color: "green"
      };
  }
};

