import React, { useEffect, useState } from "react";
import { injectIntl } from "react-intl";
import PropTypes from "prop-types";
import { useDispatch } from "react-redux";
import Pagination from "@mui/material/Pagination";
import PaginationItem from "@mui/material/PaginationItem";
import { ID_PREFIX } from "../../../../../configs/IdConfig";
import ActionDropdown from "../../../action-dropdown-mui";
import { Grid } from "@mui/material";

import FormattedMessage from "../../../formatted-message";
import {
  updateGridCurrentPageNumber,
  updateGridCurrentPageSize
} from "../../../../../state/actions/actions";

const RowPerPageText = () => (
  <FormattedMessage id="rowsPerPageText" defaultMessage="Rows per page:" />
);

/**
 * Component to render custom pagination footer.
 */
const GridFooter = ({
  usePaginationWithApi,
  totalSize,
  pageSizeOptions,
  page,
  intl,
  testId,
  onPageSizeChange,
  onPageChange,
  pageSize
}) => {
  const footerId = testId ? `${ID_PREFIX}${testId}` : "";
  // React-Redux Hooks
  const dispatch = useDispatch();
  const [activePage, setActivePage] = useState(1);
  const [startCount, setStartCount] = useState(0);
  const [endCount, setEndCount] = useState(0);

  useEffect(() => {
    setActivePage(page);
    dispatch(updateGridCurrentPageNumber(page));
  }, [page]); // eslint-disable-line react-hooks/exhaustive-deps

  useEffect(() => {
    if (activePage === 1) {
      setStartCount(1);
      pageSize >= totalSize ? setEndCount(totalSize) : setEndCount(pageSize);
    } else {
      setStartCount(pageSize * (activePage - 1));
      activePage * pageSize >= totalSize
        ? setEndCount(totalSize)
        : setEndCount(activePage * pageSize);
    }
  }, [activePage, pageSize, totalSize]);

  /**
   * to handle page change
   * page change action dispatch to datagrid component
   */
  const handlePageSelect = newPage => {
    const decrementedPage = newPage - 1;
    setActivePage(decrementedPage);
    dispatch(updateGridCurrentPageNumber(Number(decrementedPage)));
    if (usePaginationWithApi) {
      onPageChange(decrementedPage);
    }
  };

  /**
   * to handle page size change
   * dropdown menu item action dispatch to datagrid component
   */
  const handleDropdownMenuItemClick = e => {
    setActivePage(1);
    dispatch(updateGridCurrentPageSize(Number(e.value)));
    if (usePaginationWithApi) {
      dispatch(updateGridCurrentPageNumber(1));
      const gridPageSize = Number(e.value);
      onPageSizeChange(gridPageSize);
    }
  };

  const labelDisplayedRows = (from, to, count) => (
    <span id={`${footerId}_pagination`}>
      <FormattedMessage
        id="gridPaginationOnlyFromAndToText"
        values={{
          from,
          to
        }}
      />
      <FormattedMessage id="gridPaginationOnlyOfText" />
      <FormattedMessage
        id="gridPaginationOnlyCountText"
        values={{
          count
        }}
      />
    </span>
  );

  return (
    <Grid
    container
    justifyContent="space-between"
    alignItems="center"
      style={{ padding: "0.7rem" }}
    >
       <div>{labelDisplayedRows(startCount, endCount, totalSize)}</div>
      <div className="d-flex align-items-center">
        <div className="d-flex align-items-center" style={{ paddingRight: 10 }}>
          <div style={{ paddingRight: 10 }}>
            <RowPerPageText />
          </div>
          <ActionDropdown
            testId={`${footerId}_per_page_menu`}
            title={`${pageSize}`}
            value={`${pageSize}`}
            options={pageSizeOptions.map(item => ({
              key: item,
              value: item
            }))}
            onMenuItemClick={handleDropdownMenuItemClick}
            hideOutline
            customStyle={{ width: 40,marginTop:'3px' }}
          />
        </div>
        <Pagination
          count={Math.ceil(totalSize / pageSize) || 1}
          defaultPage={activePage}
          page={activePage}
          showFirstButton
          showLastButton
          color="primary"
          size="small"
          onChange={(event, newPage) => handlePageSelect(newPage)}
          renderItem={item => {
            switch (item.type) {
              case "first":
                return (
                  <PaginationItem
                    id={`${footerId}_pagination_first`}
                    aria-label={intl.formatMessage({
                      id: "goToFirstPage",
                      defaultMessage: "Go to first page"
                    })}
                    title={intl.formatMessage({
                      id: "goToFirstPage",
                      defaultMessage: "Go to first page"
                    })}
                    {...item}
                  />
                );
              case "previous":
                return (
                  <PaginationItem
                    id={`${footerId}_pagination_previous`}
                    aria-label={intl.formatMessage({
                      id: "goToPreviousPage",
                      defaultMessage: "Go to previous page"
                    })}
                    title={intl.formatMessage({
                      id: "goToPreviousPage",
                      defaultMessage: "Go to previous page"
                    })}
                    {...item}
                  />
                );
              case "page":
                return (
                  <PaginationItem
                    id={`${footerId}_pagination_${item.page}`}
                    aria-label={intl.formatMessage(
                      {
                        id: "goToParticularPage",
                        defaultMessage: `Go to page ${item.page}`
                      },
                      { pageNumber: item.page }
                    )}
                    title={intl.formatMessage(
                      {
                        id: "goToParticularPage",
                        defaultMessage: `Go to ${item.page} page`
                      },
                      { pageNumber: item.page }
                    )}
                    {...item}
                  />
                );
              case "next":
                return (
                  <PaginationItem
                    id={`${footerId}_pagination_next`}
                    aria-label={intl.formatMessage({
                      id: "goToNextPage",
                      defaultMessage: "Go to next page"
                    })}
                    title={intl.formatMessage({
                      id: "goToNextPage",
                      defaultMessage: "Go to next page"
                    })}
                    {...item}
                  />
                );
              case "last":
                return (
                  <PaginationItem
                    id={`${footerId}_pagination_last`}
                    aria-label={intl.formatMessage({
                      id: "goToLastPage",
                      defaultMessage: "Go to last page"
                    })}
                    title={intl.formatMessage({
                      id: "goToLastPage",
                      defaultMessage: "Go to last page"
                    })}
                    {...item}
                  />
                );
              default:
                return <PaginationItem {...item} />;
            }
          }}
        />
      </div>
    </Grid>
  );
};

GridFooter.defaultProps = {
  usePaginationWithApi: false,
  pageSizeOptions: [25, 50, 100]
};

GridFooter.propTypes = {
  usePaginationWithApi: PropTypes.bool,
  totalSize: PropTypes.number.isRequired,
  pageSizeOptions: PropTypes.arrayOf(PropTypes.number),
  page: PropTypes.number.isRequired,
  intl: PropTypes.shape({
    formatMessage: PropTypes.func.isRequired
  }).isRequired,
  testId: PropTypes.string.isRequired,
  onPageSizeChange: PropTypes.func.isRequired,
  onPageChange: PropTypes.func.isRequired,
  pageSize: PropTypes.number.isRequired
};

export default React.memo(injectIntl(GridFooter));
