import moment from "moment";
export function formatBytes(a, b) {
    if (!a) {
      return `0 GB`;
    }
    if (0 == a) return "0 GB";
    let c = 1024,
      d = b || 2,
      e = ["Bytes", "KB", "MB", "GB", "TB", "PB", "EB", "ZB", "YB"],
      f = Math.floor(Math.log(a) / Math.log(c));
    return parseFloat((a / Math.pow(c, f)).toFixed(d)) + " " + e[f];
  }

  export function getBrowserLocale() {
    let lang;
    if (navigator.languages && navigator.languages.length) {
      lang = navigator.languages[0]; // latest versions of Chrome and Firefox set this correctly
    } else if (navigator.userLanguage) {
      lang = navigator.userLanguage; // IE only
    } else {
      lang = navigator.language; // latest versions of Chrome, Firefox, and Safari set this correctly
    }
    return lang;
  }
  
  export const getLocaleDate = (timestamp, format, convertTS = true, lang) => {
    let locale = lang ? lang : getBrowserLocale();
    locale && moment.locale(locale);
    /*let ts = convertTS ? convertUTCtimestamp(timestamp) : timestamp;
    ts = parseInt(ts, 10);
    let formattedTime = ts && isNumber(ts) ? moment.unix(ts) : moment.unix();
    let time = format
      ? formattedTime.format(format)
      : formattedTime.format("lll");*/
  
    return moment.unix(timestamp).format(format ? format : "lll");
  };