import React, { Component, useMemo, useCallback } from "react";
import PropTypes from "prop-types";
import { compose } from "redux";
import { injectIntl } from "react-intl";
import  DataGridHelper from "../../datagrid/DataGridHelper";
import DataGrid from "../../CCReactTable-MUI";
import Checkbox from "@mui/material/Checkbox";
import ArrowDropDownIcon from "@mui/icons-material/ArrowDropDown";
import IconMUI from "../../icon/IconMui";
// import DataGrid from "../../data-grid-mui/DataGrid"
import get from "lodash/get";
import {
  withCCDataGrid,
  withLoading,
  withEntities,
  withPagination,
  withSorted,
  withChecked,
  withCheckedTbody
} from "../../CCDataGrid-MUI/HOCs";
import ActionDropdownMUI from "../../action-dropdown-button-mui/index";

const ReactTableAdapter = WrappedComponent => {
  const Ret = props => {
    const {
      ccDataGrid,
      isLoading,
      sorted,
      page,
      pages,
      pageSize,
      total,
      // columns,
      // search,
      checked,
      entities,

      // actionCreators
      updateSorted,
      updatePage,
      updatePageSize,
      setColumnWidth,
      uncheckAll,
      checkRows,
      uncheckRow,
      // Direct Props
      columns: columnsProp,
      intl,
      ...rest
    } = props;
    const {
      onActions,
      tbodyProps,
      onRowCellClick,
      processRecords,
      columnsModel,
      columnsDefinition,
      SubComponent,
      subComponentDetails,
      getData,
      testId,
      columnsData
    } = ccDataGrid;
    const {
      ccDataGridId,
      hideCheckbox,
      pagination,
      isSingleSelect,
      showPagination = true,
      localSearch = false
    } = ccDataGrid.config;

    let columns = columnsProp
      ? columnsProp
      : columnsData
      ? columnsData
      : DataGridHelper.getCCDataGridColumnDefinitions(
          columnsModel,
          columnsDefinition
        );
    let allChecked = null;
    let checkBoxColumn = [];
    let currentEntities = entities;
    if (pagination === false) {
      currentEntities = currentEntities.splice((page - 1) * pageSize, pageSize);
    }
    let processedEntities =
      (processRecords && processRecords(currentEntities)) || currentEntities;

    columns = useMemo(() => {
      if (!hideCheckbox) {
        // hideCheckbox prop is used to hide the checkbox
        checkBoxColumn = [
          {
            accessor: "_rowId",
            id: "selection",
            minWidth: 60,
            width: 60,
            maxWidth: 60,
            Header: () =>
              isSingleSelect ? (
                ""
              ) : (
                <div className="checkbox-cell">
                  <Checkbox
                    // id={`cc_id_${testId}_th_selection`}
                    checked={allChecked}
                    onClick={e => {
                      e.stopPropagation();
                      if (allChecked) {
                        uncheckAll();
                      } else {
                        checkRows(...processedEntities.map(row => row._rowId));
                      }
                    }}
                    onChange={() => {}}
                  />
                  {/* <label htmlFor={`cc_id_${testId}_th_selection`} /> */}
                </div>
              ),
            Cell: cell => (
              <div className="checkbox-cell">
                <Checkbox
                  id={`cc_id_${testId}_tr_${cell.row.id}_selection`}
                  checked={checked[cell.value] !== undefined}
                  onClick={e => {
                    e.stopPropagation();
                    if (checked[cell.value]) {
                      uncheckRow(cell.value);
                    } else {
                      if (isSingleSelect) {
                        uncheckAll();
                      }
                      checkRows(cell.value);
                    }
                  }}
                  onChange={() => {}}
                />
                <label
                  htmlFor={`cc_id_${testId}_tr_${cell.row.id}_selection`}
                />
              </div>
            )
          }
        ];
      }
      if (!onActions && !hideCheckbox) {
        allChecked =
          Object.keys(checked).length === get(processedEntities, "length");
        if (Object.keys(checked).length === 0) allChecked = false;
        columns = [
          ...checkBoxColumn,
          ...columns // TODO: add logic to filter/order columns
        ];
        return columns;
      } else if (onActions) {
        allChecked =
          Object.keys(checked).length === get(processedEntities, "length");
        if (Object.keys(checked).length === 0) allChecked = false;
        columns = [
          ...checkBoxColumn,
          {
            Header: intl.formatMessage({
              id: `cc_id_${testId}_th_available_actions`,
              defaultMessage: "Action"
            }),
            accessor: "available_actions",
            className: "no-ellipsis",
            sortable: false,
            minWidth: 80,
            width: 80,
            maxWidth: 80,
            Cell: cell => {
              return (
                <ActionDropdownMUI
                  testId={`cc_id_${testId}_tr_${cell.row.id}_avaialble_actions`}
                  title={<IconMUI icon={ArrowDropDownIcon} />}
                  options={cell.value}
                  onMenuItemClick={actionKey => {
                    onActions(
                      ccDataGridId,
                      actionKey,
                      cell.original
                        ? cell.original._rowId
                        : cell.row.original._rowId,
                      cell.original ? cell.original : cell.row.original
                    );
                  }}
                  menuAlign="left"
                  color="secondary"
                  variant="outlined"
                  size="small"
                  customButtonStyle={{
                    maxWidth: "26px",
                    maxHeight: "26px",
                    minWidth: "26px",
                    minHeight: "26px"
                  }}
                />
              );
            }
          },
          ...columns // TODO: add logic to filter/order columns
        ];

        return columns;
      } else {
        return columns;
      }
    }, [columns, checked, hideCheckbox, isSingleSelect, processedEntities]);

    const paginationCheck = localSearch && total <= 50 ? false : pagination;
    const onPageChange = useCallback(
      pageIndex => {
        return props.updatePage(
          pageIndex + 1,
          props.ccDataGrid.config.pagination
        );
      },
      [props.ccDataGrid.config.pagination]
    );

    const onPageSizeChange = useCallback(
      (pageSize, pageIndex) => {
        props.updatePage(pageIndex + 1, false); // don't refresh grid
        props.updatePageSize(pageSize, props.ccDataGrid.config.pagination);
      },
      [props.ccDataGrid.config.pagination]
    );
    const onSortedChange = useCallback(newSorted => {
      return props.updateSorted(newSorted);
    }, []);

    const onResizedChange = useCallback(newResized => {
      const { setColumnWidth } = props;
      setColumnWidth(newResized);
    }, []);
    return (
      <WrappedComponent
        manual
        hideCheckbox={hideCheckbox}
        noDataText={
          isLoading && processedEntities && processedEntities.length === 0
            ? true
            : false
        }
        data={processedEntities}
        pages={pages}
        page={page}
        onPageChange={onPageChange}
        pageSize={pageSize}
        onPageSizeChange={onPageSizeChange}
        onResizedChange={onResizedChange}
        // SubComponent={
        //   SubComponent
        //     ? row => (
        //         <SubComponent
        //           data={row.original}
        //           getData={getData}
        //           subComponentDetails={subComponentDetails}
        //         />
        //       )
        //     : null
        // }
        freezeWhenExpanded
        totalSize={total}
        sorted={sorted}
        onSortedChange={onSortedChange}
        showPagination={paginationCheck}
        usePaginationWithApi={pagination !== undefined ? pagination : true}
        // {...tbodyProps}
        columns={columns}
        testId={testId || ccDataGridId}
        SubComponent={SubComponent || null}
        subComponentDetails={subComponentDetails}
        getData={getData}
        onRowCellClick={onRowCellClick}
        {...rest}
      />
    );
  };

  Ret.propTypes = {
    ccDataGrid: PropTypes.shape().isRequired,
    isLoading: PropTypes.bool.isRequired,
    sorted: PropTypes.arrayOf(PropTypes.shape()).isRequired,
    page: PropTypes.number.isRequired,
    pages: PropTypes.number.isRequired,
    pageSize: PropTypes.number.isRequired,
    total: PropTypes.number.isRequired,
    checked: PropTypes.shape().isRequired,
    entities: PropTypes.arrayOf(PropTypes.shape()).isRequired,
    updateSorted: PropTypes.func.isRequired,
    updatePage: PropTypes.func.isRequired,
    updatePageSize: PropTypes.func.isRequired,
    uncheckAll: PropTypes.func.isRequired,
    checkRows: PropTypes.func.isRequired,
    uncheckRow: PropTypes.func.isRequired,
    columns: PropTypes.arrayOf(PropTypes.shape()),
    hideCheckbox: PropTypes.bool
  };

  return Ret;
};

const TBody = compose(
  injectIntl,
  withCCDataGrid,
  withLoading,
  withEntities,
  withPagination,
  withSorted,
  withCheckedTbody,
  ReactTableAdapter
)(DataGrid);

export default TBody;
