// } from "../../../components/common/CCDataGrid-MUI/AdvancedSearchForm";
import Button from "../../../components/common/button";
import FormattedMessage from "../../../components/common/formatted-message";
import { showDialog, Dialog } from "../../../state/actions/actions";
import { useDispatch } from "react-redux";
// import AddPersonModalForm from "./AddPersonModal";

const CCDataGrid = createCCDataGrid({
  ccDataGridId: CCDATAGRID_ID.USER_MANAGEMENT,
  primaryKey: "user_id",
  api: FETCH_SITES,
  persist: true,
  hideCheckbox: false,
  hideGridSettings: true
});

