const UMTable = () => {
  const dispatch = useDispatch();

  const ccActionHandler = (ccDataGridId:string, actionKey:string, rowId:string, cell:Array<{}>) => {
    console.log("ccActionHnadler",rowId,cell);
  };
  const ccBulkActionHandler = (
    ccDataGridId:string,
    actionKey:string,
    checkedItems:Array<string>,
    checkedEntities:Array<{}>
  ) => {
    console.log("ccBulkAction",actionKey,checkedItems,checkedEntities);
  }

  const onRowCellClickHandler = (cell:any,rowInfo:any) => {
    console.log('cell,rowInfo',cell,'rowInfo')
  }
  let sourceGroupsAttributes = {
    SITE_NAME: "site_name"
  };

  const processRecords = (sourceGroupsData:any) => {
    sourceGroupsData.forEach((sourceGroup:any) => {
      //   sourceGroup[SPOG_GRID_PROPERTIES.ID_KEY] =
      //     sourceGroup[sourceGroupsAttributes.SOURCE_GROUPS_ID];
      const groupName = sourceGroup[sourceGroupsAttributes.SITE_NAME];
      if (groupName && !groupName.hasOwnProperty("label")) {
        sourceGroup[sourceGroupsAttributes.SITE_NAME] = {};
        sourceGroup[sourceGroupsAttributes.SITE_NAME].label = groupName;
      }
    });
    return sourceGroupsData;
  };

  const handleDialog = () => {
    dispatch(showDialog(Dialog.ADD_PERSON));
  };

  const gridHeaderButtons = () => (
    <Button
    //   testId="DAzFg2"
      className="add-destination"
      color="primary"
      content={<FormattedMessage id="add.person" defaultMessage="Add Person" />}
      onClick={e => {
        e.preventDefault();
        handleDialog();
      }}
      size="large"
      variant="contained"
    />
  );

  return (
    <div style={{
      width: "100%",
      height: "100%",
      position: "relative",
      marginTop:'60px'
    }} className="">
      <CCDataGrid
        columnsModel={COLUMN_DEFS.columnData}
        columnsDefinition={COLUMN_DEFS.columns}
        gridHeaderButtons={gridHeaderButtons()}
        filterType={CCDATAGRID_ID.USER_MANAGEMENT}
        processRecords={processRecords}
        // AdvancedSearchForm={AdvancedSearch}
        onActions={ccActionHandler}
        onRowCellClick={onRowCellClickHandler}
        groupActionHandler = {ccBulkActionHandler}
        additionalParameters={{ site_type: "gateway", product_type: "udp" }}
      />
      {/* <AddPersonModalForm /> */}
    </div>
  );
};

export default UMTable;
