import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";

import * as ccDataGridActions from "../../../../state/actions/ccDataGrid";
import Grid from '@mui/material/Grid'; 

import {
  CCDataGridProvider,
  TBody,
  THeader
} from "../../CCDataGrid-MUI";

class CCTableUnconnected extends PureComponent {
  componentDidMount() {
    const {
      initialize,
      fetchData,
      config: { pollInterval }
    } = this.props;
    initialize();

    if (pollInterval) {
      this.pollTimer = setInterval(() => {
        fetchData(pollInterval);
      }, pollInterval * 1000);
    }
  }

  componentWillUnmount() {
    if (this.pollTimer) {
      clearInterval(this.pollTimer);
    }
  }

  render() {
    const {
      config,
      additionalParameters,
      children,
      onActions,
      AdvancedSearchForm = null,
      advancedSearchOptions = [],
      processRecords,
      onRowCellClick,
      filterType,
      gridHeaderButtons,
      groupActionHandler,
      addSourceOptionClick,
      columnsModel,
      columnsDefinition,
      showAddSourceComponent,
      SubComponent,
      subComponentDetails,
      getData,
      testId,
      columnsData,
      ccDataGridCloneId,
      ...tbodyProps
    } = this.props;
    let ccDataGridUniqueId = config.ccDataGridId;
    if (ccDataGridCloneId) {
      ccDataGridUniqueId = ccDataGridCloneId;
    }
    return (
      <CCDataGridProvider
        value={{
          config,
          additionalParameters,
          onActions,
          AdvancedSearchForm,
          advancedSearchOptions,
          tbodyProps,
          processRecords,
          onRowCellClick,
          filterType,
          gridHeaderButtons,
          columnsModel,
          columnsDefinition,
          SubComponent,
          subComponentDetails,
          getData,
          groupActionHandler,
          showAddSourceComponent,
          addSourceOptionClick,
          testId,
          ccDataGridUniqueId,
          columnsData
        }}
      >
        <div>
          {children.length === 0 ? (
            <div className="spog-data-grid-wrapper d-flex">
              <Grid container className="spog-data-grid-wrapper d-flex">
                <Grid item sm={12}>
              <THeader />
              </Grid>
              <Grid item sm={12}
               id="export-container-pdf"
               style={{
                 display: "flex",
                 flexDirection: "column",
                 position: "relative",
                 overflow: "auto",
                 height:'450px',
                minHeight:'200px',
                //  maxheight: "400px",
               }}> 
                <TBody />
                </Grid>
                </Grid>
              </div>
          ) : (
            <div className="spog-data-grid-wrapper d-flex">
              {children}
            </div>
          )}
        </div>
      </CCDataGridProvider>
    );
  }
}

CCTableUnconnected.propTypes = {
  initialize: PropTypes.func.isRequired,
  fetchData: PropTypes.func.isRequired,
  config: PropTypes.shape({
    pollInterval: PropTypes.number
  }).isRequired,
  additionalParameters: PropTypes.shape().isRequired,
  AdvancedSearchForm: PropTypes.func,
  children: PropTypes.oneOfType([
    PropTypes.node,
    PropTypes.arrayOf(PropTypes.node)
  ]),
  processRecords: PropTypes.func,
  advancedSearchOptions: PropTypes.shape([]),
  onActions: PropTypes.func.isRequired,
  onRowCellClick: PropTypes.func.isRequired,
  filterType: PropTypes.string.isRequired,
  gridHeaderButtons: PropTypes.element.isRequired,
  groupActionHandler: PropTypes.func.isRequired,
  addSourceOptionClick: PropTypes.func.isRequired,
  columnsModel: PropTypes.shape([]).isRequired,
  columnsDefinition: PropTypes.shape([]).isRequired,
  showAddSourceComponent: PropTypes.bool.isRequired,
  SubComponent: PropTypes.element.isRequired,
  subComponentDetails: PropTypes.element.isRequired,
  getData: PropTypes.func.isRequired,
  testId: PropTypes.string.isRequired,
  ccDataGridCloneId: PropTypes.string
};

CCTableUnconnected.defaultProps = {
  AdvancedSearchForm: null,
  advancedSearchOptions: [],
  children: null,
  processRecords: null,
  ccDataGridCloneId: ""
};

const CCTable = connect(
  () => ({}),
  (dispatch, ownProps) => ({
    initialize() {
      const {
        additionalParameters,
        defaultAdvancedSearchOptions = []
      } = ownProps;
      let { ccDataGridId, ...restConfig } = ownProps.config;
      if (ownProps.ccDataGridCloneId) {
        ccDataGridId = ownProps.ccDataGridCloneId;
      }
      dispatch(
        ccDataGridActions.initialize(
          ccDataGridId,
          restConfig,
          additionalParameters,
          defaultAdvancedSearchOptions
        )
      );
    },
    fetchData(pollInterval) {
      let polling = false;
      if (pollInterval) polling = true;
      let { ccDataGridId } = ownProps.config;
      if (ownProps.ccDataGridCloneId) {
        ccDataGridId = ownProps.ccDataGridCloneId;
      }
      dispatch(ccDataGridActions.fetchData(ccDataGridId, false, polling));
    }
  })
)(CCTableUnconnected);

export default CCTable;
